/* BASE */
html {
  box-sizing: border-box; }

* {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
  box-sizing: inherit; }

*:before,
*:after {
  box-sizing: inherit; }

input[type],
[contenteditable] {
  user-select: text; }

body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-size: 1rem;
  font-weight: 400; }

base, basefont, datalist, head, meta, script, style, title,
noembed, param, template {
  display: none; }

a {
  text-decoration: none;
  color: inherit; }

b {
  font-weight: 400; }

em,
i {
  font-style: normal; }

/*IMPORTANT: This removes the focus outline for most browsers. Be aware this is a backwards accessibilty step! Mozilla (i.e. Firefox) also adds a dotted outline around a tags and buttons when they receive tab focus which I haven't found an unhacky way of removing.*/
a,
button {
  user-select: none; }

a:focus,
button:focus {
  outline: 0; }

button {
  appearance: none;
  background-color: transparent;
  border: 0;
  padding: 0; }

input,
fieldset {
  appearance: none;
  border: 0;
  padding: 0;
  margin: 0;
  min-width: 0;
  font-size: 1rem;
  font-family: inherit; }

/* For IE, we want to remove the default cross ('X') that appears in input fields when a user starts typing - Make sure you add your own! */
input::-ms-clear {
  display: none; }

/*This switches the default outline off when an input receives focus (really important for users tabbing through with a keyboard) so ensure you put something decent in for your input focus instead!!*/
input:focus {
  outline: 0; }

input[type="number"] {
  /*Mozilla shows the spinner UI on number inputs unless we use this:*/
  -moz-appearance: textfield; }

/*Removes the little spinner controls for number type inputs (WebKit browsers/forks only)*/
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  appearance: none; }

/*SVG defaults to inline display which I dislike*/
svg {
  display: inline-flex; }

img {
  /*Make images behave responsively. Here they will scale up to 100% of their natural size*/
  max-width: 100%;
  /*Make images display as a block (UA default is usually inline)*/
  display: block; }

/*Removes the default focusring that Mozilla places on select items. From: http://stackoverflow.com/a/18853002/1147859 
Ensure you set `#000` to the colour you want your text to appear */
select:-moz-focusring {
  color: transparent;
  text-shadow: 0 0 0 #000; }

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  -webkit-overflow-scrolling: touch;
  -webkit-touch-callout: none;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
  text-size-adjust: none;
  -moz-user-select: none;
  user-select: none;
  word-wrap: break-word;
  text-rendering: optimizeLegibility;
  -webkit-backface-visibility: hidden;
  -ms-content-zooming: none; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%; }

h1 {
  font-size: 2.5rem; }

h2 {
  font-size: 2rem; }

h3 {
  font-size: 1.375rem; }

h4 {
  font-size: 1.125rem; }

h5 {
  font-size: 1rem; }

h6 {
  font-size: 0.875rem; }

p {
  font-size: 1.125rem;
  font-weight: 200;
  line-height: 1.8; }

.left {
  text-align: left; }

.right {
  text-align: right; }

.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto; }

.justify {
  text-align: justify; }

.hidden-sm {
  display: none; }

.container {
  margin-left: 10px;
  margin-right: 10px; }
  @media only screen and (min-width: 33.75em) {
    .container {
      width: calc(100% - 20px); } }
  @media only screen and (min-width: 60em) {
    .container {
      width: 100%;
      max-width: 60rem;
      margin: 0 auto; } }

.row {
  position: relative;
  margin-left: -5px;
  margin-right: -5px; }

.row [class^="col"] {
  float: left;
  padding: 0 5px;
  min-height: 0.125rem;
  transition: width .4s ease; }

.row::after {
  content: "";
  display: table;
  clear: both; }

.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  width: 100%; }

.col-1-sm {
  width: 8.33333%; }

.col-2-sm {
  width: 16.66667%; }

.col-3-sm {
  width: 25%; }

.col-4-sm {
  width: 33.33333%; }

.col-5-sm {
  width: 41.66667%; }

.col-6-sm {
  width: 50%; }

.col-7-sm {
  width: 58.33333%; }

.col-8-sm {
  width: 66.66667%; }

.col-9-sm {
  width: 75%; }

.col-10-sm {
  width: 83.33333%; }

.col-11-sm {
  width: 91.66667%; }

.col-12-sm {
  width: 100%; }

@media only screen and (min-width: 45em) {
  .col-1 {
    width: 8.33333%; }
  .col-2 {
    width: 16.66667%; }
  .col-3 {
    width: 25%; }
  .col-4 {
    width: 33.33333%; }
  .col-5 {
    width: 41.66667%; }
  .col-6 {
    width: 50%; }
  .col-7 {
    width: 58.33333%; }
  .col-8 {
    width: 66.66667%; }
  .col-9 {
    width: 75%; }
  .col-10 {
    width: 83.33333%; }
  .col-11 {
    width: 91.66667%; }
  .col-12 {
    width: 100%; }
  .hidden-sm {
    display: block; } }

.product-list {
  font-family: 'Open Sans', sans-serif; }
  .product-list .product {
    font-size: 14px;
    font-weight: 400;
    text-align: center; }
    .product-list .product .product-image {
      margin: 0 auto; }
    .product-list .product .product-title {
      color: #313135;
      font-size: 14px; }
    .product-list .product .product-price {
      font-size: 16px;
      color: #313135;
      position: relative; }
      .product-list .product .product-price strong {
        font-size: 24px;
        font-weight: 700; }
      .product-list .product .product-price:before {
        content: '';
        display: block;
        width: 15px;
        height: 2px;
        background: #dfbd00;
        position: absolute;
        left: 50%;
        margin-left: -7.5px;
        top: 5px; }
    .product-list .product .product-installments {
      color: #999999;
      font-size: 14px;
      line-height: 2px; }

.cart {
  font-family: 'Open Sans', sans-serif;
  position: fixed;
  background: #202025;
  width: 75%;
  top: 0;
  right: 0;
  color: #fff;
  z-index: 1; }
  .cart .cart-header {
    padding: 30px 0;
    text-align: center; }
    .cart .cart-header .cart-header-info {
      position: relative;
      display: inline-block; }
      .cart .cart-header .cart-header-info .cart-total {
        transition: transform .4s linear;
        border-radius: 50%;
        background-color: #dfbd00;
        width: 19px;
        height: 19px;
        color: #202025;
        font-size: 12px;
        font-weight: 700;
        line-height: 19px;
        position: absolute;
        top: 27px;
        right: -5px; }
        .cart .cart-header .cart-header-info .cart-total:empty {
          transform: scale(0); }
    .cart .cart-header .cart-title {
      color: #fff;
      font-weight: 700;
      font-size: 24px;
      vertical-align: super;
      margin-left: 20px; }
